.product__text-area-wrapper {
   display: flex;
   flex-direction: column;
   align-self: flex-start;
   width: 100%;
}

.product__desc {
   display: flex;
   align-items: center;
   height: 26px;
   gap: 2px
}

.product__price,
.vertical-divider,
.product__savings {
   font-size: 8px;

   @media ( min-width: 375px ) {
      font-size: 11px;
   }
   @media ( min-width: 600px ) {
      font-size: 14px;
   }
}

.product__price {
   display: block;
   white-space: nowrap;
   font-family: Larsseit;
   color: rgb(83, 84, 138);
   margin-top: 1px;

   @media ( min-width: 375px ) {
      margin-top: 3px;
   }
}

.vertical-divider {
   line-height: 13px;
}

.product__savings {
   font-family: Campton;
   font-weight: bold;
   color: rgb(179, 92, 30);
   margin-top: 1px;

   @media ( min-width: 375px ) {
      margin-top: 5px;
   }
}