html {
   scroll-behavior: smooth !important;
}

nav {
   position: fixed;
   top: 0px;
   z-index: 2;
   width: 100%;
}

.nav-main {
   background-color: rgb(11, 35, 65);
   color: white;
   font-size: 32px;
   font-family: "Larsseit";
   font-weight: normal;
   height: 60px;
   padding: 20px 32px 20px 16px;
   box-sizing: border-box;

   @media (min-width: 860px) {
      padding: 26px 80px;
   }
}

.nav-bar {
   display: flex;
   align-items: center;
   height: 100%;
   justify-content: space-between;

   @media (min-width: 1024px) {
      justify-content: unset;
   }
}

.nav-dropdown-area {
   background-color: rgb(11, 35, 65);
   position: relative;
   z-index: 2;
   display: flex;
   flex-direction: column;
   padding: 32px 16px 16px 32px;
   border-radius: 0 0 4px 4px;

   @media (min-width: 860px) {
      display: none;
   }
}

.nav-focus-item {
   display: flex;
   padding: 5px 0px 0px 20px;
   width: 100%;
   -webkit-box-pack: justify;
   justify-content: space-between;
   color: rgb(247, 248, 246);
   align-self: center;
   font-size: 16px;
   line-height: 18px;
   cursor: pointer;

   @media (min-width: 860px) {
      display: none;
   }
}

.nav-item,
.nav-dropdown-item {
   color: white;
   text-decoration: none;
}

.nav-item {
   display: none;
   font-size: 18px;
   padding-bottom: 14px;

   @media (min-width: 860px) {
      display: inline-block;
   }
}

.nav-item:not(:last-of-type) {
   margin-right: 48px;
}

.nav-item.active {
   border-bottom: 4px solid rgb(255, 255, 255);
}

.nav-dropdown-item { 
   display: block;
   cursor: pointer;
   padding-bottom: 40px;
   font-size: 18px;
}