.product-wrapper {
   display: flex;
   flex-direction: column;
   align-items: center;
   gap: 8px;
   width: 150px;
   height: 100%;

   @media (min-width: 400px) {
      width: 180px;
   }
   @media (min-width: 429px) {
      width: 190px;
      height: auto;
   }
   @media (min-width: 600px) {
      width: 300px;
   }
   @media (min-width: 768px) {
      width: 220px;
      gap: 12px;
   }
   @media (min-width: 856px) {
      width: 260px;
   }
   @media (min-width: 1024px) {
      width: 300px;
   }
   @media (min-width: 1280px) {
      width: 400px;
   }
}

.product__thumbnail-container {
   box-sizing: border-box;
   border: 2px solid rgb(236, 235, 231);
   border-radius: 12px;
   display: block;
   overflow: hidden;
   width: 100%;
   height: 122px;
   background: none;
   opacity: 1;
   margin: 0px;
   padding: 0px;
   position: relative;
   inset: 0px;

   @media (min-width: 390px) {
      height: 132px;
   }
   @media (min-width: 400px) {
      height: 142px;
      border-radius: 20px;
   }
   @media (min-width: 600px) {
      height: 300px;
   }
   @media (min-width: 768px) {
      height: 160px;
   }
   @media (min-width: 1024px) {
      height: 220px;
   }
   @media (min-width: 1280px) {
      height: 300px;
   }
}

.product__thumbnail {
   width: 100%;
   cursor: pointer;
   position: absolute;
   inset: 0px;
   box-sizing: border-box;
   padding: 0px;
   border: none;
   margin: auto;
   display: block;
   width: 0px;
   height: 0px;
   min-width: 100%;
   max-width: 100%;
   min-height: 100%;
   max-height: 100%;
   object-fit: contain;
}

.product__title {
   font-family: Campton;
   font-weight: 600;
   color: rgb(11, 35, 65);
   letter-spacing: -0.3px;
   font-size: 12px;
   cursor: pointer;

   @media (min-width: 425px) {
      font-size: 14px;
   }
   @media (min-width: 768px) {
      font-size: 16px;
   }
}