.products-section {
   position: relative;
}

.section-title__wrapper {
   margin: 0px auto;
   max-width: 1440px;
   padding: 32px 80px 0px;

   @media (min-width: 1280px) {
      padding: 32px 80px 0px;
   }
}

.section-title {
   font-family: "QuincyCF Regular";
   font-size: 36px;
   font-weight: 100;
   color: rgb(43, 44, 110);
   text-align: center;
   margin: 0;

   @media (min-width: 1280px) {
      font-size: 48px;
      text-align: left;
   }
}

.products-area {
   padding: 1rem 1.5rem;
   display: grid;
   grid-template-columns: 1fr 1fr;
   column-gap: 16px;
   row-gap: 10px;
   justify-items: center;
   justify-content: center;
   max-width: 1440px;
   margin: 0 auto;

   @media (min-width: 320px) {
      row-gap: 40px;
   }
   @media (min-width: 426px) {
      column-gap: unset;
   }
   @media (min-width: 768px) {
      grid-template-columns: 1fr 1fr 1fr;
      gap: 40px;
   }
}